"use client";

import React, { useEffect, useRef, useState } from "react";
import Image from "next/image";
import { FaAngleRight, FaChevronLeft, FaChevronRight, FaPlayCircle } from "react-icons/fa";
import { CiBoxList } from "react-icons/ci";

const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
const IMAGE_BASE = process.env.NEXT_PUBLIC_TMDB_IMAGE;

interface Movie {
  id: number;
  title: string;
  name?: string;
  backdrop_path: string | null;
  poster_path: string | null;
}

const Gems = () => {
  const [movies, setMovies] = useState<Movie[]>([]);
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/trending/movie/week?api_key=${API_KEY}`)
      .then((res) => res.json())
      .then((data) => setMovies(data.results || []))
      .catch(console.error);
  }, []);

  const scrollLeft = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
    scrollRef.current?.scrollBy({ left: -300, behavior: "smooth" });
  };

  const scrollRight = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
    scrollRef.current?.scrollBy({ left: 300, behavior: "smooth" });
  };

  return (
    <div className="pt-4 w-full overflow-hidden">
      {/* TITLE */}
      <div className="px-6 sm:px-10 md:px-20 mb-4">
        <h1 className="text-2xl font-bold text-white flex items-center gap-2">
          Gems for you <FaAngleRight />
        </h1>
      </div>

      {/* CAROUSEL */}
      <div className="relative px-6 sm:px-10 md:px-20 group">
        {/* LEFT BUTTON */}
        <button
          onClick={(e) => scrollLeft(e)}
          className="hidden md:flex absolute left-0 top-1/2 -translate-y-1/2 z-30
                     bg-black/60 hover:bg-black text-white p-2 rounded-full
                     opacity-0 group-hover:opacity-100 transition cursor-pointer"
        >
          <FaChevronLeft size={22} />
        </button>

        {/* RIGHT BUTTON */}
        <button
          onClick={(e) => scrollRight(e)}
          className="hidden md:flex absolute right-0 top-1/2 -translate-y-1/2 z-30
                     bg-black/60 hover:bg-black text-white p-2 rounded-full
                     opacity-0 group-hover:opacity-100 transition cursor-pointer"
        >
          
          <FaChevronRight size={22}  />
        </button>

        {/* MOVIES SCROLL */}
        <div
          ref={scrollRef}
          className="flex gap-4 py-10 "
        >
          {movies.map((movie) => {
            const imagePath =
              IMAGE_BASE && (movie.backdrop_path || movie.poster_path)
                ? `${IMAGE_BASE}${movie.backdrop_path || movie.poster_path}`
                : "/placeholder.jpg";

            return (
              <div key={movie.id} className="relative group/card w-[250px] flex-shrink-0">
                <div className="relative bg-black rounded-lg transition-all duration-300 ease-out
                                group-hover/card:scale-110 group-hover/card:-translate-y-6 overflow-visible z-10">
                  {/* IMAGE */}
                  <div className="relative h-[150px] rounded-t-lg overflow-hidden">
                    <Image
                      src={imagePath}
                      alt={movie.title || movie.name || "Movie"}
                      fill
                      className="object-cover"
                    />
                    <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent
                                    opacity-0 transition group-hover/card:opacity-100" />
                  </div>

                  {/* DETAILS PANEL */}
                  <div className="absolute top-full left-0 right-0 bg-[#181818] rounded-b-lg
                                  opacity-0 scale-95 transition-all duration-300
                                  pointer-events-none group-hover/card:opacity-100
                                  group-hover/card:scale-100 group-hover/card:pointer-events-auto
                                  z-[999] -translate-y-4">
                    <div className="flex items-center gap-3 px-4 pt-4">
                      <button className="bg-white text-black p-2 rounded-full">
                        <FaPlayCircle />
                      </button>
                      <button className="bg-gray-700 text-white p-2 rounded-full">
                        <CiBoxList />
                      </button>
                    </div>
                    <div className="px-4 py-3 text-xs text-gray-300">
                      <p className="text-green-500 font-semibold">98% Match</p>
                      <p className="mt-1">HD</p>
                      <p className="mt-1">Action â€¢ Thriller</p>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Gems;













